.preloader {
  display: none; }

.preloader.hidden {
  opacity: 0; }

body.preload {
  width: 100%;
  height: 100vh; }
  body.preload .preloader {
    position: fixed;
    z-index: 10000;
    width: 100%;
    height: 100%;
    background-color: #fff;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s opacity; }
  body.preload .preloader__dots {
    width: 4em;
    height: 4em;
    position: relative;
    text-align: center;
    animation: preloader__dots-rotate 2s infinite linear; }
    body.preload .preloader__dots .preloader__dot {
      width: 2em;
      height: 2em;
      display: inline-block;
      position: absolute;
      top: 0;
      background-color: #77b728;
      border-radius: 100%;
      animation: preloader__dots-bounce 2s infinite ease-in-out; }
    body.preload .preloader__dots .preloader__dot_2 {
      background-color: #2288e8;
      top: auto;
      bottom: 0;
      animation-delay: -1s; }

@keyframes preloader__dots-rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes preloader__dots-bounce {
  0%,
  100% {
    transform: scale(0); }
  50% {
    transform: scale(1); } }

* {
