.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25); }
  .header .burger {
    display: none; }

@media only screen and (max-width: 768px) {
  .header .burger {
    display: block;
    z-index: 5;
    padding: 10px;
    margin-right: -10px;
    cursor: pointer; }
    .header .burger .burger__line {
      width: 18px;
      height: 2px;
      background-color: #2C4053;
      border-radius: 2px;
      transition: width 0.3s 0.6s, background 0.3s 0.3s, transform 0.3s 0s; }
      .header .burger .burger__line::after, .header .burger .burger__line::before {
        content: '';
        display: block;
        background: #2C4053;
        width: 18px;
        height: 2px;
        position: relative; }
      .header .burger .burger__line::before {
        transition: width 0.3s 0.6s, bottom 0.3s 0.3s, transform 0.3s 0s;
        bottom: 5px; }
      .header .burger .burger__line::after {
        transition: width 0.3s 0.6s, top 0.3s 0.3s, transform 0.3s 0s;
        top: 3px;
        width: 10px; }
    .header .burger.active .burger__line {
      background-color: transparent;
      transition: width 0.3s 0s, background 0.3s 0.3s, transform 0.3s 0.6s; }
      .header .burger.active .burger__line::before {
        bottom: 0;
        transform: rotate(45deg);
        transition: width 0.3s 0s, bottom 0.3s 0.3s, transform 0.3s 0.6s; }
      .header .burger.active .burger__line::after {
        top: -2px;
        transform: rotate(-45deg);
        transition: width 0.3s 0s, top 0.3s 0.3s, transform 0.3s 0.6s;
        width: 18px; }
  .header .headerNav {
    position: fixed;
